import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { AppService } from '../../../../services/app.service';
import { BaseComponent } from '../../../../shared/components/base.component';

import { IUser } from '../../../../services/data/users.service';
import { ISpaceFilters } from '../../space-flow/filters/filters.component';

import * as moment from 'moment';

@Component({
    selector: 'visitor-flow-options',
    templateUrl: './options.component.html',
    styleUrls: ['./options.component.scss']
})
export class VisitorFlowOptionsComponent extends BaseComponent implements OnInit {
    @Input() public filters: ISpaceFilters;
    @Input() public attendees: IUser[];
    @Output() public filtersChange = new EventEmitter<ISpaceFilters>();
    @Output() public loading = new EventEmitter();
    @Output() public back = new EventEmitter();

    public model: any = {
        date_index: 0,
        time_index: 0,
        duration_index: 0,
        level_index: 0
    };

    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {
        if (!this.filters) {
            this.filters = {
                date: moment().valueOf(),
                duration: 30,

            };
        }
        this.subs.obs.building = this.service.Buildings.listen((bld) => {
            if (bld) {
                this.model.levels = [ ...bld.levels ];
                const exists = this.model.levels.find(i => this.filters.level && i.id === this.filters.level);
                if (exists) {
                    this.model.level_index = this.model.levels.indexOf(exists);
                    if (this.model.level_index < 0) { this.model.level_index = 0; }
                }
                this.updateForm();
            }
        });
        this.loadDates();
        this.updateForm();
    }

    public updateForm() {
        this.timeout('update', () => {
            // Prevent pre-initialisation errors
            if (!this.model.dates) { this.model.dates = [{ id: moment().seconds(0).milliseconds(0).valueOf() }]; }
            if (!this.model.times) { this.model.times = [{ id: moment().format('HH:mm') }]; }
            // Update filter fields
            const date = moment(this.model.dates[this.model.date_index].id);
            const time = this.model.times[this.model.time_index].id.split(':');
            this.filters = {
                date: date.hours(+time[0]).minutes(+time[1]).valueOf(),
                duration: 30,
                level: this.model.levels[this.model.level_index].id
            };
            this.filtersChange.emit(this.filters);
        }, 100);
    }

    public loadDates() {
        this.model.dates = [];
        const date = moment().hour(0).minutes(0).seconds(0).milliseconds(0);
        const end = moment(date).add(2, 'M');
        let offset = 0;
        for (; date.isBefore(end); date.add(1, 'd')) {
            this.model.dates.push({
                id: date.valueOf(),
                name: date.format('ddd MMMM DD, YYYY'),
                offset
            });
            offset++;
        }
        this.loadTimes();
    }

    public to(name: string, query?: any) {
        this.service.navigate(['book', 'visitor', name], query);
    }

    public toSpaces() { this.to('spaces'); }
    public cancel() { this.back.emit(); }

    public loadTimes() {
        // Clean up time
        this.filters.date = this.cleanTime(this.filters.date);
        this.model.times = [];
        const date = moment(this.filters.date);
        const now = moment();
        if (date.isAfter(now, 'd')) {
            now.hours(0).minutes(0);
        } else {
            now.minutes(Math.ceil(now.minutes() / 5) * 5);
        }
        if (now.minutes() % 15 !== 0) {
            this.model.times.push({
                id: now.format('HH:mm'),
                name: now.format('hh:mm A')
            });
            now.minutes(Math.ceil(now.minutes() / 15) * 15);
        }
        const end = moment(now).hours(23).minutes(59);
        for (; now.isBefore(end, 'm'); now.add(15, 'm')) {
            this.model.times.push({
                id: now.format('HH:mm'),
                name: now.format('hh:mm A')
            });
        }
        const time = date.format('HH:mm');
        let exists = this.model.times.find(a => a.id === time);
        if (exists) {
            this.model.time_index = this.model.times.indexOf(exists);
        } else {
            this.model.times.push({
                id: date.format('HH:mm'),
                name: date.format('hh:mm A')
            });
            this.model.times.sort((a, b) => a.id.localeCompare(b.id));
            exists = this.model.times.find(a => a.id === time);
            this.model.time_index = this.model.times.indexOf(exists);
        }
    }


    public cleanTime(datestamp, block_size: number = 5) {
        if (!datestamp) { datestamp = moment().valueOf(); }
        const now = moment().seconds(0).milliseconds(0);
        now.minutes(Math.ceil(now.minutes() / block_size) * block_size);
        let date = moment(datestamp).seconds(0).milliseconds(0);
        if (date.isBefore(now, 'm')) {
            date = moment(now);
        } else {
            date.minutes(Math.ceil(date.minutes() / block_size) * block_size);
        }
        return date.valueOf();
    }

    public book() {
        if (!this.attendees || this.attendees.length <= 0) { return this.back.emit(); }
        this.loading.emit('Processing...');
        const user = this.service.Users.current();
        const a = this.attendees;
        this.service.Visitors.add({
            title: `Visitors for ${user.name}: ${a[0].name}${a.length > 1 ? ', +' + (a.length - 1) : ''}`,
            visitors: a,
            date: this.filters.date,
            duration: this.filters.duration
        }).then(() => {
            this.loading.emit(false);
            this.to('invite');
        }, () => {
            this.loading.emit(false);
            this.service.error(`Error creating new visitor ${a.length > 1 ? 'group' : ''}`);
        });
    }
}
