import { Component, OnInit, Input, EventEmitter, Output, OnChanges, SimpleChanges } from '@angular/core';

import { AppService } from '../../../../services/app.service';
import { BaseComponent } from '../../../../shared/components/base.component';
import { IRoom } from '../../../../services/data/rooms.service';
import { Utils } from '../../../../shared/utility.class';
import { ISpaceFilters } from '../../space-flow/filters/filters.component';

import * as moment from 'moment';

@Component({
    selector: 'desk-flow-search',
    templateUrl: './search.template.html',
    styleUrls: ['./search.styles.scss']
})
export class DeskFlowSearchComponent extends BaseComponent implements OnInit, OnChanges {
    @Input() public filters: ISpaceFilters;
    @Output() public desk = new EventEmitter();
    @Output() public loading = new EventEmitter();
    @Input() public filtersChange = new EventEmitter();

    public model: any = {};

    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {
        this.subs.obs.rooms = this.service.Rooms.listen('list', (rooms) => this.search());
        if (!this.filters) { this.filters = {} as any; }
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.filters) {
            if (!this.filters) { this.filters = {} as any; }
            const date = moment(this.filters.date);
            this.model.start_date = date.format('DD/MM/YYYY');
            this.model.end_date = date.add(this.filters.duration - 1, 'm').format('DD/MM/YYYY');
            this.search();
            this.updateFilterList();
        }
    }

    public search() {
        if (!this.service.Buildings.current()) {
            setTimeout(() => this.search(), 300);
        }
        this.updateDate();
        this.loading.emit('Searching...');
        if (!this.filters) { this.filters = {} as any; }
        this.service.Rooms.available({
            date: this.filters.date,
            duration: this.filters.duration
        }).then((list) => {
            this.loading.emit(false);
            this.model.rooms = this.service.Rooms.list();
            this.model.items = list;
            this.model.search_results = list;
            for (const rm of this.model.items) {
                rm.in_use_at_time = true;
                for (const i of this.model.search_results) {
                    if (rm.id === i.id) {
                        rm.in_use_at_time = false;
                        break;
                    }
                }
            }
            this.filter();
        }, (err) => this.loading.emit(false));
    }

    public filter() {
        this.loading.emit('Filtering...');
        this.model.filtered_items = [];
        if (this.filters) {
            // Set level
            const level = this.service.Buildings.getLevel(this.filters.level);
            // Set Capacity
            const capacity = this.filters.capacity || -1;
            if (this.model.items) {
                const item_list = this.model.search_results;
                for (const rm of item_list) {
                    rm.in_use_at_time = true;
                    for (const i of this.model.search_results) {
                        if (rm.id === i.id) {
                            rm.in_use_at_time = false;
                            break;
                        }
                    }
                }
                for (const item of item_list) {
                    let match = true;
                    // Check levels
                    match = match && (!level || (level.id === -1 || item.level.id === level.id));
                    // Check client floor when external users present
                    match = match && (!this.model.has_external || !this.model.external_floors || this.model.external_floors.length <= 0 || (this.model.external_floors.indexOf(item.level.type.toLowerCase()) >= 0));
                    // Check level type
                    match = match && (!this.model.floor || (item.level.type.toLowerCase() === this.model.floor.toLowerCase()));
                    // Check capacity
                    match = match && (capacity <= 0 || item.capacity >= capacity);
                    let count = 0;
                    let found = 0;
                    for (const e of (this.filters.extras || [])) {
                        if (e.filter) { count++; }
                        for (const r_e of item.extras) {
                            if (r_e.id === e.id && e.filter) { found++; }
                        }
                    }
                    match = match && (found >= count);
                    if (match) { this.model.filtered_items.push(item); }
                }
            }
        } else {
            this.model.filtered_items = this.model.filtered_items.concat(this.model.items || []);
        }
        this.model.filtered_items.sort((a, b) => {
            const da = {
                lvl: (a.level ? a.level.name : '') || '',
                name: a.name
            };
            const db = {
                lvl: (a.level ? b.level.name : b.name) || '',
                name: b.name
            };
            return a.order - b.order || da.lvl.localeCompare(db.lvl) || da.name.localeCompare(db.name);
        });
        if (!this.model.searching) {
            this.loading.emit(false);
        }
    }

    public updateDate() {
        this.model.date = moment(this.filters.date).format('dddd D/MM/YYYY');
    }

    public changeDate(offset: number) {
        const date = moment(this.filters.date);
        date.add(offset, 'd');
        const now = moment();
        while (date.isBefore(now, 'm')) {
            date.add(1, 'd');
        }
        this.filters.date = date.valueOf();
        this.post();
        this.search();
    }

    public post() {
        this.timeout('post', () => {
            this.updateFilterList();
            this.filtersChange.emit(this.filters);
        });
    }

    public updateFilterList() {
        this.model.filter_list = [];
        const level = this.service.Buildings.getLevel(this.filters.level);
        this.model.filter_list.push({
            id: 'level',
            type: level && level.name ? level.id : -1,
            value: level ? level.name || 'Any Level' : 'Any Level'
        });
        const cap = this.filters.capacity;
        this.model.filter_list.push({
            id: 'capacity',
            type: cap && cap > 0 ? 1 : -1,
            value: cap && cap > 0 ? `${cap} ${cap === 1 ? 'person' : 'people'}` : 'Any capacity'
        });
        const date = moment(this.filters.date);
        this.model.filter_list.push({
            id: 'date',
            type: date.isValid() && date.isSameOrAfter(moment(), 'm') ? 1 : -1,
            value: date.format('hh:mm A')
        });
        this.model.filter_list.push({
            id: 'duration',
            type: this.filters.duration && this.filters.duration > 30 ? 1 : -1,
            value: Utils.humaniseDuration(this.filters.duration || 30, true)
        });
        for (const item of (this.filters.extras || [])) {
            if (item.filter) {
                this.model.filter_list.push({
                    id: 'extras',
                    type: item.id,
                    value: item.name
                });
            }
        }
    }

    public clearFilter(filter: any) {
        let change = false;
        if (filter.type !== -1) {
            if (filter.id === 'extras') {
                for (const item of (this.filters.extras || [])) {
                    if (item.filter && item.id === filter.type) {
                        item.filter = false;
                        change = true;
                    }
                }
            } else {
                switch (filter.id) {
                    case 'level':
                        this.filters.level = null;
                        change = true;
                        break;
                    case 'capacity':
                        this.filters.capacity = -1;
                        change = true;
                        break;
                    case 'duration':
                        this.filters.duration = 30;
                        change = true;
                        break;
                    case 'date':
                        const now = moment();
                        const date = moment(this.filters.date);
                        date.hours(now.hours()).minutes(Math.ceil(now.minutes() / 5) * 5);
                        this.filters.date = date.valueOf();
                        change = true;
                        break;
                }
            }
        }
        if (change) {
            this.post();
            this.search();
        }
    }

    public to(name: string, query?: any) {
        this.service.navigate(['book', 'desk', name], query);
    }

    public toBook(rm: IRoom) {
        this.loading.emit('Checking room is still available...');
        const user = this.service.Users.current();
        this.service.Bookings.add({
            attendees: [user],
            title: `Desks booking from App`,
            date: this.filters.date,
            duration: this.filters.duration,
            room: rm
        }).then(() => {
            this.loading.emit(false);
            this.to('results');
        }, () => {
            this.loading.emit(false);
            this.service.error(`Error creating booking for desk ${rm.name}`);
        });
    }

    public toMap(rm: IRoom) { this.to('map', { room_id: rm.id }); }
    public toFilters() { this.to('filters'); }
}
