import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { IUser } from '../../../../services/data/users.service';
import { AppService } from '../../../../services/app.service';
import { BaseComponent } from '../../../../shared/components/base.component';
import { Utils } from '../../../../shared/utility.class';
import { ISpaceFilters } from '../../space-flow/filters/filters.component';

@Component({
    selector: 'visitor-flow-visitor-list',
    templateUrl: './visitor-list.component.html',
    styleUrls: ['./visitor-list.component.scss']
})
export class VisitorFlowVisitorListComponent extends BaseComponent implements OnInit {
    @Input() public attendees: IUser[];
    @Output() public attendeesChange = new EventEmitter();
    @Output() public loading = new EventEmitter();
    @Output() public back = new EventEmitter();

    public model: any = {};

    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {

    }

    public search() {
        this.loading.emit('Searching...');
        const contacts = this.service.Contacts.list();
        const filtered_items = Utils.filter(this.model.search, contacts, ['name', 'email']);
        this.model.results = filtered_items;
        this.loading.emit(false);
    }

    public add(item: IUser) {
        if (!this.attendees) { this.attendees = []; }
        const exists = this.attendees.find(i => item.email === i.email);
        if (!exists) {
            this.attendees.push(item);
        }
        this.model.search = '';
        this.attendeesChange.emit(this.attendees);
    }

    public remove(item: IUser) {
        if (!this.attendees) { this.attendees = []; }
        const exists = this.attendees.find(i => item.email === i.email);
        if (exists) {
            this.attendees.splice(this.attendees.indexOf(exists), 1);
        }
        this.attendeesChange.emit(this.attendees);
    }

    public to(name: string, query?: any) {
        this.service.navigate(['book', 'visitor', name], query);
    }

    public cancel() { this.back.emit(); }
}
