
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';

import { AppService } from '../../services/app.service';
import { BaseComponent } from '../../shared/components/base.component';

@Component({
    selector: 'app-booking',
    templateUrl: './booking.template.html',
    styleUrls: ['./booking.styles.scss']
})
export class BookingComponent extends BaseComponent implements OnInit {
    public model: any = {};

    constructor(private service: AppService, private route: ActivatedRoute, private router: Router) {
        super();
    }

    public ngOnInit() {
        this.subs.obs.route = this.route.paramMap.subscribe((params) => {
            this.model.flow = '';
            if (params.has('flow')) {
                this.model.flow = params.get('flow');
            }
        });
        this.subs.obs.route = this.router.events.subscribe((e) => {
            if (e instanceof NavigationEnd && e.url.indexOf('book') < 0) {
                this.clearForm();
            }
        });
        this.service.set('BANNER.block_height', 0);
    }

    public clearForm() {
        ['visitor', 'space', 'phone-booth', 'desks'].forEach(n =>
            ['filters', 'space', 'attendees', 'breadcrumbs'].forEach(i => this.clear(n, i))
        );
    }

    public clear(flow: string, name: string) {
        if (localStorage) {
            localStorage.removeItem(`STAFF.${flow}.${name}`);
        }
    }
}
