
import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MapPinComponent } from '@acaprojects/ngx-widgets';

import { BaseComponent } from '../../../../shared/components/base.component';
import { AppService } from '../../../../services/app.service';
import { IRoom } from '../../../../services/data/rooms.service';

@Component({
    selector: 'space-flow-view-space',
    templateUrl: './view-space.template.html',
    styleUrls: ['./view-space.styles.scss']
})
export class SpaceFlowViewSpaceComponent extends BaseComponent implements OnInit {
    @Output() public back = new EventEmitter();
    @Output() public level = new EventEmitter();

    public model: any = {};

    constructor(private service: AppService, private route: ActivatedRoute) {
        super();
    }

    public ngOnInit() {
        this.subs.obs.params = this.route.queryParamMap.subscribe((params) => {
            if (params.has('room_id')) {
                this.model.room_id = params.get('room_id');
                this.focus();
            }
        });
    }

    public focus() {
        if (this.model.room_id) {
            const room: IRoom = this.service.Rooms.item(this.model.room_id);
            if (room) {
                this.model.room = room;
                this.level.emit(room.level.name);
                this.model.focus = {
                    id: `area-${room.map_id}-status`,
                    zoom: 150
                };
                this.model.poi = [{
                    id: `area-${room.map_id}-status`,
                    cmp: MapPinComponent
                }];
            } else {
                this.back.emit();
            }
        } else {
            this.back.emit();
        }
    }
}
