import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { AppService } from '../../../services/app.service';
import { BaseComponent } from '../../../shared/components/base.component';

@Component({
    selector: 'desk-booking-flow',
    templateUrl: './desk-flow.component.html',
    styleUrls: ['./desk-flow.component.scss']
})
export class DeskFlowComponent extends BaseComponent implements OnInit {

    public model: any = {};

    constructor(private service: AppService, private route: ActivatedRoute, private router: Router) {
        super();
        this.model.breadcrumbs = [];
    }

    public ngOnInit(): void {
        this.model.flow = 'desks';
        this.load('breadcrumbs');
        this.subs.obs.params = this.route.paramMap.subscribe((params) => {
            if (this.model.page && !this.model.ignore) {
                this.model.breadcrumbs.push(this.model.page);
                this.store('breadcrumbs');
            }
            this.model.ignore = false;
            this.model.page = '';
            if (params.has('page')) {
                this.model.page = params.get('page');
            }
            if (!this.model.page) { this.clearForm(); }
        });
        this.subs.obs.route = this.router.events.subscribe((e) => {
            if (e instanceof NavigationEnd && e.url.indexOf('book/desk') < 0) {
                this.clearForm();
            }
        });
        this.load('attendees');
    }

    public store(name: string) {
        if (localStorage) {
            const d = this.model[name];
            const value = typeof d === 'object' ? JSON.stringify(d) : d;
            localStorage.setItem(`STAFF.${this.model.flow}.${name}`, value);
        }
    }

    public load(name: string) {
        if (localStorage) {
            const item = localStorage.getItem(`STAFF.${this.model.flow}.${name}`);
            if (item) {
                try {
                    this.model[name] = JSON.parse(item);
                } catch (e) {
                    this.model[name] = item;
                }
            }
        }
    }

    public clear(name: string) {
        if (localStorage) {
            localStorage.removeItem(`STAFF.${this.model.flow}.${name}`);
        }
    }

    public storeFilters() { this.store('filters'); }
    public loadFilters() { this.load('filters'); }

    public storeSpace() { this.store('space'); }
    public loadSpace() { this.load('space'); }

    public clearForm() {
        ['filters', 'space', 'breadcrumbs'].forEach(i => this.clear(i));
    }

    set loading(value: any) {
        this.timeout('loading', () => this.model.loading = value, 10);
    }

    public to(name: string, query?: any) {
        this.service.navigate(['book', 'desk', name], query);
    }

    public back() {
        const previous = this.model.breadcrumbs.pop();
        if (previous) {
            this.to(previous);
            this.model.ignore = true;
            this.store('breadcrumbs');
        } else {
            this.service.navigate(['book']);
        }
    }
}
