import { Component, OnInit, Input, EventEmitter, Output, OnChanges, SimpleChanges } from '@angular/core';

import { AppService } from '../../../../services/app.service';
import { BaseComponent } from '../../../../shared/components/base.component';
import { IUser } from '../../../../services/data/users.service';

@Component({
    selector: 'space-flow-confirm-attendees',
    templateUrl: './confirm-attendees.template.html',
    styleUrls: ['./confirm-attendees.styles.scss']
})
export class SpaceFlowConfirmAttendeesComponent extends BaseComponent implements OnInit {
    @Input() public attendees: IUser[];
    @Output() public attendeesChange = new EventEmitter();
    @Output() public loading = new EventEmitter();
    @Output() public back = new EventEmitter();

    public model: any = {};

    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {
        this.model.flow = this.service.get('SPACE.flow');
    }

    public remove(item: IUser) {
        const exists = this.attendees.find(u => u.email === item.email);
        if (exists) {
            this.attendees.splice(this.attendees.indexOf(exists), 1);
            this.attendeesChange.emit(this.attendees);
        }
    }

    public to(name: string, query?: any) {
        this.service.navigate(['book', 'space', name], query);
    }

    public cancel() { this.back.emit(); }
    public findTime() { this.to('find-time'); }
    public toConfirm() { this.to('confirm-details'); }
}
