import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { BaseComponent } from '../../../../shared/components/base.component';
import { AppService } from '../../../../services/app.service';
import { Utils } from '../../../../shared/utility.class';
import { ISpaceFilters } from '../filters/filters.component';

import * as moment from 'moment';

@Component({
    selector: 'space-flow-search-options',
    templateUrl: './search-options.template.html',
    styleUrls: ['./search-options.styles.scss']
})
export class SpaceFlowSearchOptionsComponent extends BaseComponent implements OnInit {
    @Input() public filters: ISpaceFilters;
    @Output() public filtersChange = new EventEmitter<ISpaceFilters>();

    public model: any = {
        date_index: 0,
        time_index: 0,
        duration_index: 0,
        level_index: 0,
        capacity_index: 0
    };

    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {
        if (!this.filters) { this.filters = {} as any; }
        this.subs.obs.capacity = this.service.Rooms.listen('capacities', (list) => {
            this.model.capacity = list || [];
            const exists = this.model.capacity.find(i => i.id === this.filters.capacity);
            if (exists) { this.model.capacity_index = this.model.capacity.indexOf(exists); }
            this.updateForm();
        });
        this.subs.obs.building = this.service.Buildings.listen((bld) => {
            if (bld) {
                this.model.levels = [{ id: null, name: 'Any' }, ...bld.levels];
                const exists = this.model.levels.find(i => this.filters.level && i.id === this.filters.level);
                if (exists) { this.model.level_index = this.model.levels.indexOf(exists); }
                this.model.extras = [...bld.extras];
                this.model.extras.forEach(item => this.filters.extras.forEach(old => {
                    if (old.id === item.id) { item.filter = old.filter; }
                }));
                this.updateForm();
            }
        });
        this.loadDates();
        this.updateForm();
    }

    public updateForm() {
        this.timeout('update', () => {
            // Prevent pre-initialisation errors
            if (!this.model.dates) { this.model.dates = [{ id: moment().seconds(0).milliseconds(0).valueOf() }]; }
            if (!this.model.times) { this.model.times = [{ id: moment().format('HH:mm') }]; }
            if (!this.model.durations) { this.model.durations = [{ id: 30 }]; }
            if (!this.model.capacity) { this.model.capacity = [{ id: -1 }]; }
            if (!this.model.levels) { this.model.levels = [{ id: null }]; }
            if (this.model.level_index < 0) { this.model.level_index = 0; }
            if (this.model.capacity_index < 0) { this.model.capacity_index = 0; }
            // Update filter fields
            console.log('Model:', this.model);
            const date = moment(this.model.dates[this.model.date_index].id);
            const time = this.model.times[this.model.time_index].id.split(':');
            this.filters = {
                date: date.hours(+time[0]).minutes(+time[1]).valueOf(),
                duration: this.model.durations[Math.min(this.model.durations.length - 1, this.model.duration_index)].id,
                capacity: this.model.capacity[Math.min(this.model.capacity.length - 1, this.model.capacity_index)].id,
                level: this.model.levels[Math.min(this.model.levels.length - 1, this.model.level_index)].id,
                extras: this.model.extras && this.model.extras.length > 0 ? [...this.model.extras] : []
            };
            this.loadTimes();
            this.loadDurations();
            this.filtersChange.emit(this.filters);
        }, 100);
    }

    public loadDates() {
        this.model.dates = [];
        const date = moment().hour(0).minutes(0).seconds(0).milliseconds(0);
        const end = moment(date).add(2, 'M');
        let offset = 0;
        for (; date.isBefore(end); date.add(1, 'd')) {
            this.model.dates.push({
                id: date.valueOf(),
                name: date.format('ddd MMMM DD, YYYY'),
                offset
            });
            offset++;
        }
        this.loadTimes();
    }

    public to(name: string, query?: any) {
        this.service.navigate(['book', 'space', name], query);
    }

    public toSpaces() {
        this.service.set('SPACE.flow', 'space');
        this.to('spaces');
    }
    public toAttendees() {
        this.service.set('SPACE.flow', 'attendees');
        this.to('attendees');
    }
    public toFilters() {
        this.service.set('SPACE.flow', 'space');
        this.to('filters');
    }

    public loadTimes() {
        // Clean up time
        this.filters.date = this.cleanTime(this.filters.date);
        this.model.times = [];
        const date = moment(this.filters.date);
        const now = moment();
        now.minutes(Math.ceil(now.minutes() / 5) * 5);
        if (now.minutes() % 15 !== 0) {
            this.model.times.push({
                id: now.format('HH:mm'),
                name: now.format('hh:mm A')
            });
            now.minutes(Math.ceil(now.minutes() / 15) * 15);
        }
        if (date.isAfter(now, 'd')) {
            now.hours(0).minutes(0);
        }
        const end = moment(now).hours(23).minutes(59);
        for (; now.isBefore(end, 'm'); now.add(15, 'm')) {
            this.model.times.push({
                id: now.format('HH:mm'),
                name: now.format('hh:mm A')
            });
        }
        const time = date.format('HH:mm');
        let exists = this.model.times.find(a => a.id === time);
        if (exists) {
            this.model.times.sort((a, b) => a.id.localeCompare(b.id));
            this.model.time_index = this.model.times.indexOf(exists);
        } else {
            this.model.times.push({
                id: date.format('HH:mm'),
                name: date.format('hh:mm A')
            });
            this.model.times.sort((a, b) => a.id.localeCompare(b.id));
            exists = this.model.times.find(a => a.id === time);
            this.model.time_index = this.model.times.indexOf(exists);
        }
        this.loadDurations(true);
    }

    public cleanTime(datestamp, block_size: number = 5) {
        if (!datestamp) { datestamp = moment().valueOf(); }
        const now = moment().seconds(0).milliseconds(0);
        now.minutes(Math.ceil(now.minutes() / block_size) * block_size);
        let date = moment(datestamp).seconds(0).milliseconds(0);
        if (date.isBefore(now, 'm')) {
            date = moment(now);
        } else {
            date.minutes(Math.ceil(date.minutes() / block_size) * block_size);
        }
        return date.valueOf();
    }

    public loadDurations(init: boolean = false) {
        this.filters.date = this.cleanTime(this.filters.date);
        const date = moment(this.filters.date);
        const end = moment(this.filters.date).add(240, 'm');
        date.add(30, 'm');
        let duration = 30;
        this.model.durations = [];
        for (; date.isBefore(end, 'm'); date.add(15, 'm')) {
            if (init && this.filters.duration && duration === this.filters.duration) {
                this.model.duration_index = this.model.durations.length;
            }
            this.model.durations.push({
                id: duration,
                name: `${date.format('hh:mm A')} (${Utils.humaniseDuration(duration)})`
            });
            duration += 15;
        }
    }
}
