
import { Routes } from '@angular/router';
import { BookingComponent } from './booking.component';
import { BookingFlowFeedbackComponent } from './feedback/feedback.component';

export const ROUTES: Routes = [
    { path: '', component: BookingComponent },
    { path: 'feedback', component: BookingFlowFeedbackComponent },
    { path: ':page', component: BookingComponent },
    { path: ':flow/:page', component: BookingComponent },
    { path: '**',      redirectTo: '' }
];
