import { Component, OnInit, Input, EventEmitter, Output, OnChanges, SimpleChanges } from '@angular/core';

import { AppService } from '../../../../services/app.service';
import { BaseComponent } from '../../../../shared/components/base.component';
import { IUser } from '../../../../services/data/users.service';
import { Utils } from '../../../../shared/utility.class';

@Component({
    selector: 'space-flow-attendees',
    templateUrl: './attendees.template.html',
    styleUrls: ['./attendees.styles.scss']
})
export class SpaceFlowAttendeesComponent extends BaseComponent implements OnInit, OnChanges {
    @Input() public attendees: IUser[];
    @Output() public attendeesChange = new EventEmitter();
    @Output() public loading = new EventEmitter();
    @Output() public back = new EventEmitter();

    public model: any = {};

    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {
        if (!this.attendees) { this.attendees = []; }
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.filters) {
            this.search();
        }
    }

    public search() {
        this.loading.emit('Searching...');
        if (!this.attendees) { this.attendees = []; }
        if (!this.model.external) {
            this.service.Users.query({ q: this.model.search }).then((list: IUser[]) => {
                    this.loading.emit(false);
                    this.model.users = list;
                    this.filter();
                }, (err) => this.loading.emit(false));
        } else {
            this.loading.emit(false);
            this.model.users = Utils.filter((this.model.search || ''), this.service.Contacts.list(), ['name', 'email']);
            this.filter();
        }
    }

    public filter() {
        (this.model.users || []).forEach(item => {
            const exists = this.attendees.find(a  => a.email === item.email);
            if (exists) { item = exists; }
        });
        this.loading.emit('Filtering...');
        this.model.filtered_items = this.model.users;
        this.loading.emit(false);
    }

    public add(user: IUser) {
        if (!this.model.attendees) { this.model.attendees = []; }
        const exists = this.model.attendees.find(a => a.email === user.email);
        if (exists) {
            (user as any).added = false;
            this.model.attendees.splice(this.model.attendees.indexOf(exists), 1);
        } else {
            (user as any).added = true;
            this.model.attendees.push(user);
        }
    }

    public new() {
        this.to('new-contact');
    }


    public to(name: string, query?: any) {
        this.service.navigate(['book', 'space', name], query);
    }

    public cancel() { this.back.emit(); }

    public toConfirm() {
        if (!this.attendees) { this.attendees = []; }
        if (this.model.attendees) {
            this.model.attendees.forEach(item => {
                const exists = this.attendees.find(a => a.email === item.email);
                if (!exists) {
                    this.attendees.push(item);
                }
            });
            this.attendeesChange.emit(this.attendees);
        }
        this.to('confirm-attendees');
    }
}
