import { Component, OnInit, Input, EventEmitter, Output, SimpleChanges, OnChanges, ViewChild, Renderer2, ElementRef } from '@angular/core';

import { AppService } from '../../../../services/app.service';
import { ISpaceFilters } from '../filters/filters.component';
import { BaseComponent } from '../../../../shared/components/base.component';
import { IUser } from '../../../../services/data/users.service';
import { IRoom } from '../../../../services/data/rooms.service';

import * as moment from 'moment';

@Component({
    selector: 'space-flow-confirm-details',
    templateUrl: './confirm-details.template.html',
    styleUrls: ['./confirm-details.styles.scss']
})
export class SpaceFlowConfirmDetailsComponent extends BaseComponent implements OnInit, OnChanges {
    @Input() public form: ISpaceFilters;
    @Input() public attendees: IUser[];
    @Input() public space: IRoom;
    @Input() public from: string;
    @Output() public formChange = new EventEmitter();
    @Output() public attendeesChange = new EventEmitter();
    @Output() public loading = new EventEmitter();
    @Output() public back = new EventEmitter();

    public model: any = {};

    @ViewChild('desc') private description: ElementRef;

    constructor(private service: AppService, private renderer: Renderer2) {
        super();
    }

    public ngOnInit() {
        this.updateDisplay();
        this.init();
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.form) {
            this.model.title = this.form.title || this.model.title;
            this.model.description = this.form.description || this.model.description;
            console.log('Description:', this.model.description);
            this.updateDescription();
        }
        this.updateDisplay();
    }

    public init() {
        if (!this.service.ready()) {
            return this.timeout('init', () => this.init());
        }
        this.model.settings = this.service.Settings.get('app.booking') || {};
    }

    public change($event) {
        this.model.description = $event.target.textContent;
        this.store();
    }

    public updateDescription() {
        if (!this.description) {
            return this.timeout('desc', () => this.updateDescription());
        }
        const div = this.description.nativeElement;
        this.renderer.setProperty(div, 'textContent', this.model.description);
    }

    public store() {
        this.form.title = this.model.title;
        this.form.description = this.model.description;
        this.formChange.emit(this.form);
    }

    public removeAttendee(item: IUser) {
        const exists = (this.attendees || []).find(i => i.email === item.email);
        if (exists) {
            this.attendees.splice(this.attendees.indexOf(exists), 1);
            this.attendeesChange.emit(this.attendees);
        }
    }

    public updateDisplay() {
        if (!this.form) { this.form = {} as any; }
        const date = moment(this.form.date);
        this.model.date = date.format('dddd D MMM YYYY');
        const start = date.format('hh:mm A');
        const end = date.add(this.form.duration || 30, 'm').format('hh:mm A');
        this.model.period = `${start} - ${end}`;
    }

    public book() {
        if (this.space) {
            this.loading.emit('Checking room status...');
            this.service.Rooms.isAvailable(this.space.id, this.form.date, this.form.duration)
                .then(() => {
                    this.createBooking();
                }, (e) => {
                    this.loading.emit(false);
                    const start = moment(this.form.date);
                    const date = start.format('MMM Do, YYYY');
                    const end = moment(start).add(this.form.duration, 'm');
                    this.service.error('Room is not available at the selected time anymore');
                    this.model.error = JSON.parse(JSON.stringify(this.model.error));
                });
        } else {
            this.loading.emit('Finding available room...');
                this.service.Rooms.available({
                date: this.form.date,
                duration: this.form.duration
            }).then((list: IRoom[]) => {
                const rm_list = list.filter(item  => item.capacity >= (this.attendees || []).length || 1);
                if (rm_list && rm_list.length > 0) {
                    this.space = rm_list[Math.floor(Math.random() * rm_list.length)];
                    this.createBooking();
                } else {
                    this.service.error('No rooms found at the specified time with the required capacity');
                    this.loading.emit(false);
                }
            });
        }
    }

    private createBooking() {
        const title = this.model.title;
        if (!this.model.title) {
            this.model.title = `From Staff App by ${this.model.user.name}`;
        }
        if (this.model.settings.title_prefix) {
            this.model.title = this.model.settings.title_prefix + this.model.form.title;
        }
        this.loading.emit('Requesting meeting...');
        const form = {
            ...this.form,
            title: this.model.title,
            description: this.model.description,
            attendees: this.attendees,
            room: this.space
        };
        this.service.Bookings.add(form).then(() => {
            this.loading.emit(false);
            this.to('result');
        }, (err) => {
            this.loading.emit(false);
            if (this.model.settings && this.model.settings.title_prefix) {
                this.model.title = this.model.title.replace(this.model.settings.title_prefix, '');
            }
            this.model.title = title;
            if (!err || err.indexOf('user') < 0) {
                this.service.error('An error occurred while making your booking');
            }
        });
    }

    public to(name: string, query?: any) {
        this.service.navigate(['book', 'space', name], query);
    }

    public cancel() { this.back.emit(); }
    public toAttendees() { this.to('attendees'); }
    public toSpaces() { this.to('spaces'); }
}
