import { Component, OnInit, Input, Output, EventEmitter, ViewChild, ElementRef, SimpleChanges, OnChanges } from '@angular/core';

import { BaseComponent } from '../../../../shared/components/base.component';
import { AppService } from '../../../../services/app.service';
import { IUser } from '../../../../services/data/users.service';
import { ISpaceFilters } from '../filters/filters.component';

import * as moment from 'moment';
import { Utils } from '../../../../shared/utility.class';

@Component({
    selector: 'space-flow-select-time',
    templateUrl: './select-time.template.html',
    styleUrls: ['./select-time.styles.scss']
})
export class SpaceFlowSelectTimeComponent extends BaseComponent implements OnInit, OnChanges {
    @Input() filters: ISpaceFilters;
    @Input() attendees: IUser[];
    @Output() filtersChange = new EventEmitter();
    @Output() back = new EventEmitter();

    public model: any = {};

    @ViewChild('block') private block: ElementRef;
    @ViewChild('time') private time: ElementRef;

    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {
        this.model.settings = this.service.Settings.get('app.booking') || {};
        this.generateDay();
        this.updateDisplay();
        this.interval('update', () => this.generateDay(), 2 * 60 * 1000);
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.attendees) {
            this.disableInUseBlocks();
        }
    }

    public generateDay() {
        this.model.blocks = [];
        const date = moment(this.filters.date);
        date.minutes(Math.ceil(date.minutes() / 5) * 5);
        const now = moment();
        now.minutes(Math.floor(now.minutes() / 5) * 5);
        const time = moment(date).hours(6).minutes(0);
        const end = moment(time).hours(20).minutes(0);
        for (; time.isBefore(end); time.add(5, 'm')) {
            this.model.blocks.push({
                id: time.format('HH:mm'),
                display: time.format('HH:mm'),
                hour: time.minutes() === 0,
                show: time.minutes() % 30 === 0,
                disabled: time.isBefore(now, 'm')
            });
        }
        this.disableInUseBlocks();
    }

    public move(e) {
        this.timeout('move', () => {
            if (!this.filters) { this.filters = {} as any; }
            if (!this.model.offset) { this.model.offset = { x: 0, y: 0 }; }
            if (this.model.move && this.block) {
                const center = {
                    x: e.touches && e.touches.length > 0 ? e.touches[0].clientX || e.clientX : e.clientX,
                    y: e.touches && e.touches.length > 0 ? e.touches[0].clientY || e.clientY : e.clientY
                };
                const off = this.model.move === 'bottom' ? { x: 0, y: 0 } : this.model.offset;
                const content_box = this.block.nativeElement.getBoundingClientRect();
                const percent_w = ((center.x - off.x) - content_box.left) / content_box.width;
                const percent_h = ((center.y - off.y) - content_box.top) / content_box.height;
                const percent = this.model.vertical ? percent_w : percent_h;

                const start_time = this.model.settings.start_hour || 6;
                const end_time = (this.model.settings.end_hour || 20);
                const diff_time = end_time - start_time;
                const block_size = this.model.settings.block_size || 15;
                const hour = Math.ceil((diff_time * percent + start_time) * (60 / block_size)) / (60 / block_size);
                if (this.model.move === 'bottom') {
                    let date = moment(this.filters.date);
                    const end = moment(this.filters.date).hours(Math.floor(hour)).minutes(Math.floor((hour * 60) % 60));
                    if (end.isSameOrBefore(date, 'm')) {
                        date = moment(end).add(-this.filters.duration, 'm');
                        this.filters.date = date.valueOf();
                    } else {
                        const duration = Math.floor(moment.duration(end.diff(date)).asMinutes());
                        this.filters.duration = Math.max(30, (duration || block_size));
                    }
                } else if (this.model.move === 'top') {
                    const date = moment(this.filters.date).hours(Math.floor(hour)).minutes(Math.floor((hour * 60) % 60));
                    this.filters.date = date.valueOf();
                }
                this.post();
            }
        }, 10);
    }

    public setOffset(e) {
        if (this.time) {
            const box = this.time.nativeElement.getBoundingClientRect();
            this.model.offset = { x: e.center.x - box.left, y: e.center.y - box.top };
        }
    }

    public post() {
        this.timeout('post', () => {
            this.filtersChange.emit(this.filters);
        }, 100);
        this.updateDisplay();
    }

    public to(name: string, query?: any) {
        this.service.navigate(['book', 'space', name], query);
    }

    public toSpaces() { this.to('spaces'); }
    public cancel() { this.back.emit(); }

    public changeDate(offset: number) {
        const date = moment(this.filters.date);
        date.add(offset, 'd');
        const now = moment().hours(0).minutes(0);
        while (date.isBefore(now, 'm')) {
            date.add(1, 'd');
        }
        this.filters.date = date.valueOf();
        this.generateDay();
        this.post();
    }

    public disableInUseBlocks() {
        if (!this.model.blocks) {
            return this.timeout('disable_blocks', () => this.disableInUseBlocks());
        }
        this.model.blocks.forEach(item => item.in_use = false);
        for (const user of (this.attendees || [])) {
            if (user && !user.optional && !user.external) {
                const date = moment(this.filters.date);
                this.service.Bookings.show(user.email, {
                    from: date.hours(0).minutes(0).seconds(0).unix(),
                    to: date.hours(23).minutes(59).seconds(59).unix()
                }).then((list) => {
                    (list || []).forEach(bkn => {
                        const bkn_start = moment(bkn.date);
                        if (bkn_start.isSame(date, 'd')) {
                            bkn_start.minutes(Math.floor(bkn_start.minutes() / 5) * 5);
                            const bkn_end = moment(bkn_start).add(bkn.duration, 'm');
                            for (; bkn_start.isBefore(bkn_end, 'm'); bkn_start.add(5, 'm')) {
                                const format = bkn_start.format('HH:mm');
                                const blk = this.model.blocks.find(item => item.id === format);
                                if (blk) { blk.in_use = true; }
                            }
                        }
                    });
                });
            }
        }
    }

    public updateDisplay() {
        if (!this.filters) { this.filters = {} as any; }
        if (!this.filters.date) {
            const date = moment().seconds(0).milliseconds(0);
            date.minutes(Math.ceil(date.minutes() / 5) * 5);
            this.filters.date = date.valueOf();
        }
        if (!this.filters.duration) { this.filters.duration = 30; }
        const start_time = this.model.settings.start_hour || 6;
        const end_time = (this.model.settings.end_hour || 20);
        const start = moment(this.filters.date);
        start.minutes(Math.ceil(start.minutes() / 5) * 5);
        const end = moment(start).add(this.filters.duration, 'm');
        const dur = Utils.humaniseDuration(this.filters.duration, true);
        this.model.display = `${start.format('hh:mm A')} - ${end.format('hh:mm A')} (${dur})`;
        this.model.date = `${start.format('dddd D/MM/YYYY')}`;
        this.model.active_start = ((start.hours() + start.minutes() / 60) - start_time) / (end_time - start_time);
        this.model.active_length = (this.filters.duration / 60) / (end_time - start_time);
        this.model.past = start.isBefore(moment());
    }
}
