import { Component, OnInit } from '@angular/core';
import { AppService } from '../../../../services/app.service';
import { BaseComponent } from '../../../../shared/components/base.component';

@Component({
    selector: 'desk-flow-result',
    templateUrl: './result.component.html',
    styleUrls: ['./result.component.scss']
})
export class DeskFlowResultComponent extends BaseComponent implements OnInit {
    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {
        this.clearForm();
    }

    public clear(name: string) {
        if (localStorage) {
            localStorage.removeItem(`STAFF.desk.${name}`);
        }
    }

    public clearForm() {
        this.clear('filters');
        this.clear('space');
        this.clear('attendees');
    }

    public toHome() { this.service.navigate('home'); }
    public toFeedback() { this.service.navigate(['book', 'feedback']); }
}
