import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';

import { AppService } from '../../../services/app.service';
import { BaseComponent } from '../../../shared/components/base.component';

@Component({
    selector: 'booking-flow-feedback',
    templateUrl: './feedback.template.html',
    styleUrls: ['./feedback.styles.scss']
})
export class BookingFlowFeedbackComponent extends BaseComponent {
    @Output() public loading = new EventEmitter();

    public model: any = {};

    constructor(private service: AppService) {
        super();
    }

    public change($event) {
        this.model.description = $event.target.textContent;
    }

    public toHome() { this.service.navigate('home'); }

    public submit() {
        this.loading.emit('Sending feedback...');
        this.timeout('feedback', () => {
            this.loading.emit(false);
            this.service.success('Thank you for your feedback');
            this.toHome();
        }, 2 * 1000);
    }

}
