import { Component, OnInit, Input, Output, EventEmitter, ViewChild, ElementRef, QueryList } from '@angular/core';
import { Router } from '@angular/router';

import { AppService } from '../../../../services/app.service';
import { BaseComponent } from '../../../../shared/components/base.component';
import { Utils } from '../../../../shared/utility.class';

import { IUser } from '../../../../services/data/users.service';

@Component({
    selector: 'visitor-flow-new-contact',
    templateUrl: './new-contact.component.html',
    styleUrls: ['./new-contact.component.scss']
})
export class VisitorFlowNewContactComponent extends BaseComponent implements OnInit {
    @Input() public attendees: IUser[];
    @Output() public attendeesChange = new EventEmitter<IUser[]>();
    @Output() public back = new EventEmitter();

    public model: any = {};

    public font_size = 16;
    public list_height = 0;

    @ViewChild('ref') private reference: ElementRef;
    @ViewChild('input') private input: ElementRef;

    constructor(private service: AppService, private router: Router) {
        super();
        this.model.titles = ['Mr', 'Ms', 'Mrs'];
        this.model.purposes = ['Business', 'Project', 'Delivery', 'Interview', 'Maintenance ', 'Social', 'Training', 'Other'];
    }

    public ngOnInit() {
        this.model.org_index = 0;
        this.subs.obs.building = this.service.Buildings.listen((bld) => {
            if (bld) {
                this.model.orgs = bld.orgs || [];
                if (this.model.orgs.length <= 0) {
                    this.model.orgs.push({ id: -1, name: 'No companies' });
                }
                this.list_height = Math.min(8, this.model.orgs.length);
            }
        });
        this.model.space = this.router.url.indexOf('space') >= 0;
    }

    public cancel() { this.back.emit(); }

    public add(next: boolean = false) {
        let error = false;
        // if (!this.model.first_name) { this.model.error_first_name = 'First & Middle names are required'; error = true; }
        // if (!this.model.last_name) { this.model.error_last_name = 'Last name is required'; error = true; }
        if (!this.model.email) { this.model.error_email = 'Email is required'; error = true; }
        if (!Utils.validateEmail(this.model.email)) { this.model.error_email = 'A valid email is required'; error = true; }
        if (error) { return error; }
        const user: IUser = {
            id: Math.random().toString(),
            last_name: this.model.last_name,
            first_name: this.model.first_name,
            name: `${this.model.first_name} ${this.model.last_name}`,
            email: this.model.email,
            organisation_name: this.model.org_name,
            phone: this.model.phone,
            title: this.model.title,
            purpose: this.model.purpose,
            external: true,
            type: 'external',
            image: ''
        };
        if (!this.attendees) { this.attendees = []; }
        const exists = this.attendees.find(i => i.email === user.email);
        if (!exists) { this.attendees.push(user); }
        this.attendeesChange.emit(this.attendees);
        if (next) {
            this.model.space ? this.to('confirm-attendees') : this.back.emit();
        } else {
            this.service.info(`Added contact "${this.model.name || this.model.email.split('@')[0]}"`);
            this.model = {
                ...this.model, name: '', email: '', phone: '', title: '', purpose: '', org_name: ''
            };
            this.checkOrg();
            this.model.title_index = -1;
            this.model.purpose_index = -1;
        }
    }

    public to(name: string, query?: any) {
        const loc = this.model.space ? 'space' : 'visitor';
        this.service.navigate(['book', loc, name], query);
    }

    public checkOrg() {
        if (!this.model.org_name) { this.model.org_name = ''; }
        const exists = this.model.orgs.find(i => i.name.toLowerCase() === this.model.org_name.toLowerCase());
        this.model.org_index = this.model.orgs.indexOf(exists);
    }

    public setOrg(item) {
        if (item && item.name !== 'No companies') {
            this.model.org_name = item.name;
            this.model.org_index = this.model.orgs.indexOf(item);
            this.model.show = false;
        }
    }

    public updateView() {
        if (!this.input) {
            return this.timeout('update_view', () => this.updateView());
        }
        this.timeout('update_view', () => {
            const el = document.getElementsByClassName('company-new-contact');
            if (el && el.length >= 2) {
                (el[0] as HTMLInputElement).blur();
                (el[1] as HTMLInputElement).focus();
            }
        }, 200);
    }
}
