import { Component, OnInit, Input, SimpleChanges, OnChanges } from '@angular/core';
import { AppService } from '../../../../services/app.service';
import { BaseComponent } from '../../../../shared/components/base.component';
import { ISpaceFilters } from '../../space-flow/filters/filters.component';

import * as moment from 'moment';

@Component({
    selector: 'visitor-flow-invite',
    templateUrl: './invite.component.html',
    styleUrls: ['./invite.component.scss']
})
export class VisitorFlowInviteComponent extends BaseComponent implements OnInit, OnChanges {
    @Input() public filters: ISpaceFilters;

    public model: any = {};

    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {
        if (!this.filters) { this.filters = {} as any; }
        this.updateDisplay();
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.filters) {
            this.updateDisplay();
        }
    }

    public to(name: string, query?: any) {
        this.service.navigate(['book', 'visitor', name], query);
    }

    public updateDisplay() {
        if (!this.filters) { this.filters = {} as any; }
        const date = moment(this.filters.date || null);
        if (!this.model.display) { this.model.display = {}; }
        this.model.display.date = date.format('dddd DD/MM/YYYY');
        this.model.display.time = date.format('hh:mmA');
        if (this.filters.level) {
            const level = this.service.Buildings.getLevel(this.filters.level);
            if (level) {
                this.model.display.level = `${level.name} Lobby`;
            }
        }
    }

}
